.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
    85deg,
    transparent,
    var(--color-primary),
    transparent);
    display: grid;
    place-items: center;

}

